id {\r\n        margin: 0;\r\n\r\n        .row {\r\n            width: 105% !important;\r\n        }\r\n\r\n        #create_tables th,\r\n        #create_tables td {\r\n            vertical-align: middle;\r\n        }\r\n    }\r\n\r\n    #spreadsheet_container {\r\n        #create_tables_wrapper {\r\n            width: 100% !important;\r\n        }\r\n\r\n        table {\r\n            width: 100%;\r\n\r\n            th,\r\n            td {\r\n                max-width: 200px;\r\n                overflow-wrap: break-word;\r\n            }\r\n\r\n            th:focus {\r\n                outline: none;\r\n            }\r\n        }\r\n\r\n        #create_tables_paginate > div {\r\n            padding-top: 0 !important;\r\n        }\r\n\r\n        .dt-buttons {\r\n            display: flex;\r\n            justify-content: flex-end;\r\n            margin-top: 5px;\r\n\r\n            button {\r\n                font-size: 13px;\r\n                margin: 0 5px;\r\n            }\r\n        }\r\n\r\n        .filtering_input {\r\n            display: flex;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            margin: 15px 0 5px 0;\r\n\r\n            #create_tables_length {\r\n                margin-right: auto;\r\n            }\r\n\r\n            #create_tables_filter {\r\n                margin-left: auto;\r\n            }\r\n        }\r\n\r\n        .bottom_options {\r\n            display: flex;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            margin-top: 10px;\r\n\r\n            .dataTables_info {\r\n                padding-top: 0 !important;\r\n                margin-right: auto;\r\n            }\r\n\r\n            #create_tables_paginate {\r\n                margin-left: auto;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.gswpts_create_table_container,\r\n.gswpts_tables_container {\r\n    @include mixin.button_style;\r\n    @include mixin.input_style;\r\n    @include mixin.pagination_style;\r\n    @include mixin.datatable_semantic;\r\n    @include mixin.loader_style;\r\n}\r\n\r\n.gswpts_tables_container {\r\n    table {\r\n        th,\r\n        td {\r\n            max-width: 200px;\r\n            min-width: 90px;\r\n            overflow-wrap: break-word;\r\n            border: 1px solid #76767647;\r\n        }\r\n\r\n        th:focus {\r\n            outline: none;\r\n        }\r\n    }\r\n\r\n    select {\r\n        width: 70px;\r\n        border-radius: 5px;\r\n        border: 1px solid #00000033 !important;\r\n        background: transparent !important;\r\n    }\r\n\r\n    .dt-buttons {\r\n        display: flex;\r\n        justify-content: flex-end;\r\n        margin-top: 5px;\r\n        gap: 10px;\r\n\r\n        button {\r\n            font-size: 13px;\r\n            margin: 0 6px;\r\n        }\r\n\r\n        .export_btns {\r\n            display: none;\r\n        }\r\n    }\r\n\r\n    .gswpts_tables_content {\r\n        table {\r\n            width: 100% !important;\r\n        }\r\n\r\n        .filtering_input {\r\n            display: flex;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            margin: 15px 0 5px 0;\r\n\r\n            #create_tables_length {\r\n                margin-right: auto;\r\n            }\r\n\r\n            #create_tables_filter {\r\n                margin-left: auto;\r\n            }\r\n\r\n            input {\r\n                background: transparent !important;\r\n            }\r\n        }\r\n\r\n        .bottom_options {\r\n            display: flex;\r\n            justify-content: space-between;\r\n            align-items: center;\r\n            margin-top: 10px;\r\n\r\n            .dataTables_info {\r\n                padding-top: 0 !important;\r\n                margin-right: auto;\r\n            }\r\n\r\n            #create_tables_paginate {\r\n                margin-left: auto;\r\n            }\r\n\r\n            .pagination.menu {\r\n           